import React from 'react'
import { Utils } from 'pcmli.umbrella.uni-core'

//TODO: Refactor this to the format just like the recompose
export const withServiceData = (WrappedComponent, asyncService, objectName) => {

  class With extends React.Component {

    state = {loading: false, serviceData: null}

    _onRefresh = () => {

      this.setState({loading: true})

      //pass the instance to the callee
      asyncService(this)
        .then((serviceData) => {
          this.setState({serviceData, loading: false})
        }).catch(() => {
        this.setState({loading: false})
      })
    }

    componentWillMount () {
      this._onRefresh()
    }

    render () {
      const {serviceData, loading} = this.state

      objectName = objectName || 'serviceData'
      const data = {}
      data[objectName] = serviceData

      return <WrappedComponent
        onRefresh={this._onRefresh}
        loading={loading}
        {...data}
        {...this.props}/>
    }
  }

  With.displayName = `WithServiceData(${Utils.ComponentDisplayName(WrappedComponent)})`
  return With

}



