import {graphql} from 'react-apollo'
import gql from 'graphql-tag'
import {compose, mapProps} from 'recompose'
import _ from 'lodash'

export const withRole = ({roleName} = {}) => {

  const withRoleQuery = gql`query hasRole($name: String!) {
      hasRole(name: $name)
    }
    `
  const withRoleConfig = {
    options: (ownProps) => {
      const name = ownProps.roleName || roleName


      //include the fetch policy it can re-use the cache
      return {
        skip: !name,
        variables: {name},
        fetchPolicy: 'cache-first',
        name: 'hasRole'
      }
    },
    props: ({data}) => {
      const {hasRole = false} = data
      return {hasRole}
    },
    skip: ownProps => !(ownProps.roleName || roleName) //when there is no roleName do not execute this operation
  }

  return compose(
    graphql(withRoleQuery, withRoleConfig),
    mapProps((ownProps) => {
      const roleNameProps = ownProps.roleName || roleName
      let role = _.pick(ownProps, ['hasRole'])

      role = {...role, roleName: roleNameProps}
      let result = _.omit(ownProps, ['hasRole'])
      //combined them as new props
      result = {...result, role}
      return result
    })
  )
}



