import React from 'react'
import wrapDisplayName from 'recompose/wrapDisplayName'

export const withMongoSearch = () => {

  return (WrappedComponent) => {
    class With extends React.Component  {

      handleSearch = (searchValue) => {

        const {onSearch, fields} = this.props

        let filterObject = {}

        //if searchValue  is NOT empty, then build a searchString
        if (searchValue !== "") {
          filterObject ={$or: []}

          //iterate thru all fields if there is something to seach
          fields && fields.forEach((m) => {
            let obj = {}

            if (m.type && m.type.toLowerCase() === 'array') {
              obj[m.name] = {$elemMatch: {$regex: searchValue, $options: 'i'}}
            }
            else if (m.type && m.type.toLowerCase() === 'objectarray') {

              // the name should followed by the following format
              //contacts.$.address1.gps.lat, take note about the dollar sign to indicate about the array
              //split the m.name by "."
              const objectArrayName = m.name.split('.', 3)
              //the first item is the name
              obj[objectArrayName[0]] = {$elemMatch: {[objectArrayName[2]]: {$regex: searchValue, $options: 'i'}}}
            }
            else {
              obj[m.name] = {$regex: searchValue, $options: 'i'}
            }
            filterObject['$or'].push(obj)

          })

        }

        let searchString = JSON.stringify(filterObject)

        onSearch({searchString, searchValue, searchObject: filterObject, searchFields: fields})

      }

      render () {

        const mongoSearch = {
          handleSearch: this.handleSearch
        }

        return <WrappedComponent {...mongoSearch} {...this.props} />
      }
    }

    With.displayName = wrapDisplayName(WrappedComponent, 'withMongoSearch')

    return With

  }

}



