import { action } from '@storybook/addon-actions'

export const ResolvedPromise = (message = 'What a success') => {
  return new Promise((resolve) => {
    setTimeout(() => {
      action('Resolved Promise')(message)
      resolve()
    }, 3000)

  })
}

export const RejectedPromise = (message = 'Sucks, it fails') => {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      action('Rejected Promise')(message)
      reject()
    }, 3000)

  })
}

export const ButtonOnPress = (message) => {
  GenericAction({message, actionName: 'BUTTON_PRESS'})
}
export const GenericAction = ({message, actionName} = {}) => {
  message = message || 'The action was committed'
  actionName = actionName || 'Common action'
  action(actionName)(message)
}


