import React from 'react'
import {graphql} from 'react-apollo'
import { buildQueryVariables, mergeExtraConfig } from 'pcmli.umbrella.uni-core'
import _ from 'lodash'

export const withDocument = ({query, entity, extraOptions, extraProps, paramsToFilter}) => {

  const config = {
    options: (ownProps) => {

      let searchString = {_id: null}
      if (paramsToFilter) {
        searchString = paramsToFilter({params, ownProps})
      }
      else {
        let _id = _.get(ownProps, 'params.id') || ownProps.id || ownProps._id

        //but if there is no value for _id then submit it with null values
        searchString = {_id: !_id ? null : _id}
      }

      searchString = JSON.stringify(searchString)

      let result = {
        variables: {
          ...buildQueryVariables(0, searchString, null, 1),
        }
      }

      //merge the extra configuration
      result = mergeExtraConfig(result, extraOptions)

      //then just return the new config
      return result
    },
    props: (args) => {

      const {data} = args
      const {loading, fetchMore, error, variables, refetch} = data || {}

      let item = data && data[entity] && data[entity].length > 0 && data[entity][0]

      let result = {loading, fetchMore, error, variables, refetch, item, document: item, onRefresh: refetch}

      //merge the extra configuration
      result = mergeExtraConfig(result, extraProps, args)

      return result
    },
  }

  return graphql(query, config)

}



