import React from 'react'
import {graphql} from 'react-apollo'
import _ from 'lodash'
import update from 'immutability-helper'
import {mergeExtraConfig, Utils} from 'pcmli.umbrella.uni-core'

const getMethodName = ({_type, methodName, actionName}) => {

  let localMethodName = 'onUpdate', localActionName = 'Update'
  if (_type === 'create' || _type === 'insert') {
    localMethodName = 'onCreate'
    localActionName = 'Create'
  }
  else if (_type === 'delete') {
    localMethodName = 'onDelete'
    localActionName = 'Delete'
  }

  //but if there is no value for methodName and actionName, then use the local
  methodName = methodName || localMethodName
  actionName = actionName || localActionName

  return {methodName, actionName}
}

const buildUpdateQueries = ({mutationParams, entity, queryName, mutationName}) => {
  const {_type} = mutationParams

  //if query name was not specified then user the model name
  queryName = queryName || entity

  //if mutation name was not specified then user the model name
  mutationName = mutationName || entity

  if (_type === 'update') return null

  const updateQueries = {
    [queryName]: (previousResult, {mutationResult}) => {
      const mutateResult = mutationResult.data[mutationName]

      if (_type === 'delete') {

        const index = _.findIndex(previousResult[entity], {_id: mutateResult._id})
        if (index === -1)
          throw new Error('Cannot find the id')

        return update(previousResult, {[entity]: {$splice: [[index, 1]]}})
      }
      // else if (_type == 'insert') {
      //   return update(previousResult, {[entity]: {$unshift: [mutateResult]}})
      // }
    }
  }

  return updateQueries
}

export const withMutation = ({_type, paramsToDoc, query, entity, refetchQueries, before, after, methodName, actionName, extraOptions, extraProps, paramsToFilter}) => {

  const getMethodNameResult = getMethodName({_type, methodName, actionName})
  const mutationName = `$mutate{entity}`
  refetchQueries = refetchQueries || [`${entity}`]

  const graphqlConfig = {
    name: mutationName,
    props: (args) => {

      const {ownProps} = args

      //if extraProps  is function then execute it passing the ownProps, else this is an object so just use it
      const xProps = (_.isFunction(extraProps) ? extraProps({ownProps}) : extraProps) || {}

      const afterName = `after${getMethodNameResult.actionName}`
      const beforeName = `before${getMethodNameResult.actionName}`

      let result = {
        [getMethodNameResult.methodName]: async (params) => {

          let doc = {}
          if (paramsToDoc) {
            doc = await paramsToDoc({params, ownProps})
          }

          let filter = _.get(params, 'filter') || _.get(ownProps, 'filter')
          let _id = _.get(doc, '_id') || _.get(params, '_id')

          if (paramsToFilter) {
            filter = paramsToFilter({params, ownProps})
          }

          //TODO: This should be tested
          if (!filter) {
            //if filter is not available then build it using the _id, this is required anyway for mutation
            filter = {_id}
          }

          //TODO: Should be tested
          doc = _.omit(doc, ['_id']) //should remove _id from doc object

          const docString = JSON.stringify(doc)
          const filterString = filter && JSON.stringify(filter)

          //get the id from doc, if it dies not exists there, then get the value from the params
          const variables = {
            _id,
            _type,
            input: docString,
            filter: filterString
          }

          let {optimisticResponse, update} = xProps

          const getOptimisticResponse = async ({optimisticResponse}) => {
            if (_.isFunction(optimisticResponse)) {
              if (Utils.isPromise(optimisticResponse)) {
                return await optimisticResponse({ownProps, doc})
              }
              else {
                return optimisticResponse({ownProps, doc})
              }
            }
            else {
              return optimisticResponse
            }
          }

          optimisticResponse = await getOptimisticResponse({optimisticResponse}) || undefined

          //call the actual mutation
          return args[mutationName]({variables, optimisticResponse, update})
        },
        [beforeName]: before,
        [afterName]: after
      }

      result = mergeExtraConfig(result, extraProps, args)
      return result

    },
    options: (ownProps) => {
      let result = {refetchQueries}

      //if extraOptions is function then execute it passing the ownProps, else this is an object so just use it
      const xOption = _.isFunction(extraOptions) ? extraOptions({ownProps}) : extraOptions

      result = mergeExtraConfig(result, xOption)

      return result
    }
  }

  return graphql(query, graphqlConfig)

}



