import _ from 'lodash'

export class Analytics {
  constructor ({segment, writeKey} = {}) {
    if (!segment) throw new Error('Segment.io is required')
    if (!writeKey) throw new Error('Segment writeKey is required')

    this.segment = segment
    this.segment.initialize({androidWriteKey: writeKey, iosWriteKey: writeKey})

  }

  identify = (userId, traits = {}) => {
    if (!userId) throw new Error('User id is required for analytics identify')
    if (!_.isEmpty(traits))
      this.segment.identifyWithTraits(userId, traits)
    else
      this.segment.identify(userId)

  }

  track = (event, properties = {}) => {
    if (!event) throw new Error('Event is required for analytics track')
    if (!_.isEmpty(properties))
      this.segment.trackWithProperties(event, properties)
    else
      this.segment.track(event)
  }

  screen = (screenName, properties = {}) => {
    if (!screenName) throw new Error('Screen name is required for analytics screen')
    if (!_.isEmpty(properties))
      this.segment.screenWithProperties(screenName, properties)
    else
      this.segment.screen(screenName)
  }

}



