import React from 'react'
import wrapDisplayName from 'recompose/wrapDisplayName'


export const withMongoSort = () => {

  return (WrappedComponent) => {
    class With extends React.Component {

      directionConverter = (direction) => {
        return direction.toLowerCase() === 'asc' ? 1 : -1
      }

      handleSort = (active, direction) => {
        let {fields, onSort} = this.props

        const sortObj = {
          sort: {
            [active.name]: this.directionConverter(direction)
          }
        }

        let sortString = JSON.stringify(sortObj)
        const sortValue = {name: active.name, direction: this.directionConverter(direction)}

        onSort({sortString, sortObject: sortObj, sortValue, sortFields: fields})
      }

      render () {

        const mongoSort = {
          handleSort: this.handleSort
        }

        return <WrappedComponent {...mongoSort} {...this.props} />
      }
    }

    With.displayName = wrapDisplayName(WrappedComponent, 'withMongoSort')

    return With

  }

}



