import React from 'react'
import { graphql } from 'react-apollo'

import { buildQueryVariables, mergeExtraConfig } from 'pcmli.umbrella.uni-core'
import _ from 'lodash'

const buildFetchMoreQuery = (modelName, fetchMore, skip, searchString, sortObject) => {

  const variables = buildQueryVariables(skip, searchString, sortObject)
  const updateQuery = (previousResult, {fetchMoreResult}) => {

    if (!fetchMoreResult) { return previousResult }

    return Object.assign({}, previousResult, {
      [modelName]: [...previousResult[modelName], ...fetchMoreResult[modelName]],
    })

  }

  return fetchMore({
    variables,
    updateQuery,
  })
}

export const withList = ({query, entity, extraOptions, extraProps, extraConfig = {}, paramsToFilter}) => {

  const config = {
    options: (ownProps) => {
      const {sortObject, limit} = ownProps
      let {searchString} = ownProps

      if (paramsToFilter) {
        searchString = JSON.stringify(paramsToFilter({params, ownProps}))
      }

      let result = {
        variables: buildQueryVariables(0, searchString, sortObject, limit),
        notifyOnNetworkStatusChange: true,
      }

      const xOption = _.isFunction(extraOptions) ? extraOptions({ownProps}) : extraOptions

      result = {...result, ...xOption}

      return result

    },
    props: (args) => {
      const {data} = args
      let {loading, fetchMore, error, variables, refetch, pageInfo} = data || {}

      let items = data && data[entity]
      //get the pageInfo from server for backward compatibility, and if it's not available then use the new way
      pageInfo = pageInfo || (data && data[`${entity}Meta`])

      let result = {
        loading,
        error,
        variables,
        refetch,
        items,
        pageInfo,
        loadMoreEntries (skip, searchString, sortObject) {
          return buildFetchMoreQuery(entity, fetchMore, skip, searchString, sortObject)
        }
      }

      result = mergeExtraConfig(result, extraProps, args)

      return result

    },
    ...extraConfig
  }

  return graphql(query, config)
}



