import React from 'react'
import { reduxForm, SubmissionError } from 'redux-form'
import wrapDisplayName from 'recompose/wrapDisplayName'
import { compose } from 'recompose'
import { Utils } from 'pcmli.umbrella.uni-core'
import _ from 'lodash'
import { NetworkError } from '../common/NetworkError'

export const withForm = (config = {}) => {
  //setup the redux-form configuration
  const defaultValue = {
    //form: 'AppCommonReduxForm',
    enableReinitialize: true,
    touchOnBlur: false,
    touchOnChange: true,
  }

  config = Object.assign({}, defaultValue, config)

  //generate a random name if it is not specified
  if (!config.form)
    config.form = `form_${Utils.generateId(5)}`

  const {getInitialValues} = config

  return (WrappedComponent) => {
    class With extends React.Component {

      state = {initialValues: {}}
      saveText = 'Save'
      showDelete = false

      constructor (props) {
        super(props)
        const {onCreate, onDelete} = this.props

        if (onCreate) {
          this.saveText = 'Add'
        }

        if (onDelete) {
          this.showDelete = true
        }

      }

      handleError = (errors, {doc, type}) => {

        const throwError = (message) => {
          const networkError = _.includes(message, 'Network error')
          if (networkError)
            throw new NetworkError('NO_NETWORK', {message})
          else {
            const {onError} = this.props
            onError && onError(`${message} `)
            throw new Error(message)
          }

        }

        const {errored, onError,} = this.props
        const {graphQLErrors} = errors

        //execute the function if it's available
        errored && errored(errors, doc)

        //check the type to determine which error to throw
        const typeName = `on${Utils.humanize(type)}Error` //e.g.onCreateError, onDeleteError, onUpdateError
        const typedError = this.props[typeName]
        typedError && typedError({errors, doc})

        if (graphQLErrors && graphQLErrors.length > 0) {
          const errorMessageString = graphQLErrors[0].message

          const parseError = Utils.jsonTryParse(errorMessageString)

          if (parseError !== false) {
            errors = parseError

            const {validationError} = errors[0]

            if (validationError) {
              onError && onError('Validation errors. Please update the fields highlighted as red')
              throw  new SubmissionError(validationError)
            }
            else {
              let message = errors[0].message
              if (!message)
                message = errors[0]
              throwError(message)

            }
          }
          else {
            errors = graphQLErrors
            let message = errors[0].message
            if (!message)
              message = errors[0]
            throwError(message)

          }
        }
        else {
          //default error message when it got errors
          throwError(errors.message)
        }
      }

      onFormSubmit = ({doc, action, afterAction, beforeAction, type}) => {

        const formAction = () => {
          const onlineOp = () => action(doc)
            .then((response) => {
              const {reset} = this.props

              //call the form submission reset
              reset && reset()

              //execute the after the operation
              afterAction && afterAction({response, doc, ownProps: this.props})

              //return also the result here
              return response
            })
            .catch(errors => {
              this.handleError(errors, {doc, type})
            })

          const {offlineQuery} = this.props

          //if there is no offlineQuery object then fall  back to original implementation
          if (!offlineQuery) return onlineOp()

          //if there is, check the value if online or no
          return offlineQuery.isOffline().then((isOffline) => {

            if (isOffline) {


              //if offline execute the operation inside the promise
              return new Promise((resolve) => {
                //execute the function
                action(doc)

                const {reset} = this.props

                //call the form submission reset
                reset && reset()

                //execute the after the operation
                const response = {offline: true}
                afterAction && afterAction({response, doc, ownProps: this.props})

                //just resolve it to exit this promise
                resolve(response)
              })
            }
            else {
              //when online, just execute the normal operation
              return onlineOp()
            }
          })

        }

        if (beforeAction) {
          return beforeAction({doc, action: formAction, ownProps: this.props})
        }
        else {
          return formAction()
        }
      }

      onUpdateAction = (doc) => {
        let {onUpdate, beforeUpdate, afterUpdate} = this.props
        beforeUpdate = config.beforeUpdate || beforeUpdate
        afterUpdate = config.afterUpdate || afterUpdate

        return this.onFormSubmit({
          doc,
          action: onUpdate,
          afterAction: afterUpdate,
          beforeAction: beforeUpdate,
          type: 'update'
        })
      }

      onDeleteAction = (doc) => {
        let {onDelete, beforeDelete, afterDelete} = this.props
        beforeDelete = config.beforeDelete || beforeDelete
        afterDelete = config.afterDelete || afterDelete

        return this.onFormSubmit({
          doc,
          action: onDelete,
          afterAction: afterDelete,
          beforeAction: beforeDelete,
          type: 'delete'
        })

      }

      onCreateAction = (doc) => {

        let {onCreate, beforeCreate, afterCreate} = this.props
        beforeCreate = config.beforeCreate || beforeCreate
        afterCreate = config.afterCreate || afterCreate

        return this.onFormSubmit({
          doc,
          action: onCreate,
          afterAction: afterCreate,
          beforeAction: beforeCreate,
          type: 'create'
        })

      }

      onCancelAction = () => {
        const {onCancel, reset} = this.props

        //call the form submission reset
        reset && reset()

        onCancel && onCancel()
      }

      onSubmit = (doc) => {
        const {onCreate} = this.props

        let saveAction = this.onUpdateAction
        if (onCreate)
          saveAction = this.onCreateAction

        return saveAction(doc)

      }

      componentWillMount () {
        if (getInitialValues) {
          const that = this
          getInitialValues(this.props).then((initialValues) => {
            that.setState({initialValues})
          })
        }
      }

      render () {

        const {initialValues} = this.state

        const formProps = {
          onDelete: this.onDeleteAction,
          onCreate: this.onCreateAction,
          onUpdate: this.onUpdateAction,
          onCancel: this.onCancelAction,
          onSubmit: this.onSubmit,
          showDelete: this.showDelete,
          saveText: this.saveText,
          handleError: this.handleError
        }

        return (
          <WrappedComponent
            formProps={formProps}
            initialValues={initialValues}
            {...this.props}/>
        )
      }
    }

    With.displayName = wrapDisplayName(WrappedComponent, 'withForm')

    With = compose(
      reduxForm(config),
    )
    (With)

    return With

  }

}




