import React from 'react'
import { compose } from 'recompose'
import wrapDisplayName from 'recompose/wrapDisplayName'
import gql from 'graphql-tag'
import _ from 'lodash'
import { withDocument } from '../withDocument'

export const getUserStatus = ({user}) => {
  const {tenantId, status, roles = [], _id} = user || {}
  const hasTenant = !!(tenantId)
  const isPending = status === 'pending'
  const isActive = status === 'active'
  const isInActive = status === 'inactive'
  const loggedIn = !!_id
  const isSuperAdmin = _.includes(roles, 'superAdmin')
  return {
    loggedIn,
    hasTenant,
    isPending,
    isActive,
    isInActive,
    isSuperAdmin
  }
}

export const withUser = (config = {}) => {
  let {query} = config

  query = query || gql`query allUser($filter: String, $options: String) {
  allUser(filter: $filter, options: $options ) {
    _id
    status
    tenantId
    driverId
    profile {
      name
      picture
      email
    }
    roles
  }
}
`

  return (WrappedComponent) => {
    class With extends React.Component {

      getStatus = () => {
        const {document} = this.props
        return getUserStatus({user: document})
      }

      _logout = () => {
        const {logout, history} = this.props
        logout && logout({history})
      }

      redirectAuthenticatedUser = ({redirectPath}) => {
        const {authRedirect} = this.props

        return authRedirect.authenticatedUser({
          redirectPath,
          userStatus: this.getStatus()
        })

      }

      render () {

        const {document, loading, loggingIn} = this.props
        const status = this.getStatus()

        const user = {
          ...status,
          loading,
          loggingIn,
          logout: this._logout,
          redirectAuthenticatedUser: this.redirectAuthenticatedUser,
          ...document
        }

        return <WrappedComponent user={user} {...this.props} />
      }
    }

    With.displayName = wrapDisplayName(WrappedComponent, 'withUser')
    With = compose(
      withDocument({query, entity: 'allUser'}),
    )(With)

    return With

  }

}



