import React from 'react'
import _ from 'lodash'
import { compose, withState, withHandlers } from 'recompose'

const modes = ['edit', 'view']

export const withModeState = withState('mode', 'toggleView', 'view')
export const withModeHandlers = withHandlers({
  onToggle: ({toggleView}) => event => {
    toggleView(mode => {

      //if it cannot find any value then indexOf will return -1
      let index = _.indexOf(modes, mode)

      //if the value is already at the end get back on the first item
      if (index + 1 >= modes.length)
        index = -1

      //always increment the value to get the next index
      index++

      //this will be the new value for mode
      return modes[index]
    })
  }
})

export const withViewToggle = compose(withModeState, withModeHandlers)




